.city-guide {
    &__cover-wrap {
        position: relative;
        height: 150px;
        padding: 10px 40px;
        background-color: $base-c; }

    &__cover {
        display: block;
        position: absolute;
        right: 0;
        bottom: 15px;
        width: 390px;
        height: 70px; }

    &__cover-wobbler {
        width: 160px;
        margin-bottom: 20px; }

    &__cover-icon {
        position: absolute;
        top: 25px;
        right: 110px;
        width: 85px;
        height: 85px;
        color: $base-c;
        line-height: 85px;
        background-color: $light-c;
        border-radius: 50%;
        text-align: center;

        &.icon-hotel {
            font-size: 48px; }
        &.icon-food {
            font-size: 38px; }
        &.icon-activities {
            font-size: 32px; }
        &.icon-transport {
            font-size: 45px; }
        &.icon-phones {
            font-size: 45px; } }

    &__img-figcaption {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center; }

    &__title {
        max-width: 350px;
        font-family: $bold-f;
        font-size: 36px;
        line-height: 1.1;
        letter-spacing: 0.02em;
        color: $light-c; }

    &__image {
        margin-top: 40px; }

    &__slider {
        margin-top: 40px; }

    &__separator {
        margin-top: 60px;
        margin-bottom: 60px; } }

.city-guide-item {
    &__section {
        & + .city-guide-item__section {
            margin-top: 90px; } }

    &__cover {
        margin-bottom: 30px; }

    &__text-wrap {
        width: 640px;
        margin: 0 auto; }

    &__title {
        position: relative;
        margin-bottom: 20px;
        font-size: 36px;
        font-family: $bold-f; }

    &__title-icon {
        position: absolute;
        left: -110px;
        top: -12px;
        width: 70px;
        height: 70px; }

    &__subtitle {
        margin-bottom: 20px;
        font-size: 24px;
        font-family: $bold-f; }

    &__additional-info-wrap {
        margin-bottom: 20px; } }

.city-guide-note {
    display: flex;
    align-items: baseline;
    margin-top: 40px;

    &__icon {
        margin-right: 10px;
        font-size: 14px;
        color: $base-c; }

    &__text {
        max-width: 360px; } }

.city-guide-table {
    width: 100%;
    margin-top: 30px;
    border-collapse: collapse;
    border-spacing: 0;
    font-size: $base-fz;

    &__tr {
        border-bottom: 1px solid #7a8ba1;

        &:first-child {
            border-top: 1px solid #7a8ba1; } }

    &__th {
        font-family: $bold-f;
        text-align: left;

        &_center {
            text-align: center; } }

    &__td {
        padding: 20px 0;
        vertical-align: top; }

    &__td-phone {
        color: $dark-c;
        text-decoration: none;
        white-space: nowrap;

        &:hover {
            color: $base-c; } }

    &__img {
        width: 24px;
        height: 24px;
        vertical-align: bottom;

        &:nth-child(2) {
            margin-right: 10px; }

        &:nth-child(3) {
            margin-left: 30px;
            margin-right: 10px; } } }

.city-guide-table_emergency,
.city-guide-table_repairs {
    tr {
        td:nth-child(2) {
            width: 330px;
            padding-left: 20px; } } }

.city-guide-table_bus-inner-city {
    tr {
        td:nth-child(1),
        th:nth-child(1) {
            width: 110px; }

        td:nth-child(1), {
            font-family: $bold-f; }

        td:nth-child(2),
        th:nth-child(2) {
            width: 300px;
            padding-left: 40px;
            padding-right: 30px; }

        td:nth-child(3),
        th:nth-child(3) {
            text-align: center; } } }

.city-guide-table_bus-outer-city {
    tr {
        td:nth-child(1),
        th:nth-child(1) {
            width: 110px; }

        td:nth-child(1), {
            font-family: $bold-f; }

        td:nth-child(2),
        th:nth-child(2) {
            width: 270px;
            padding-left: 40px;
            padding-right: 30px; }

        td:nth-child(3),
        th:nth-child(3) {
            width: 170px;
            padding-right: 20px; }

        td:nth-child(4),
        th:nth-child(4) {
            text-align: center; } } }

.city-guide-item__taxi-grid {
    display: flex;
    margin-left: -10px;
    margin-right: -10px; }

.taxi-grid-item {
    margin-left: 10px;
    margin-right: 10px;
    text-align: center;

    &__img {
        display: block;
        margin-bottom: 20px; }

    &__title {
        margin-bottom: 15px;
        font-family: $bold-f; }

    &__phone {
        display: block;
        text-decoration: none;
        color: $dark-c;

        &:hover {
            color: $base-c; }

        .icon-phone {
            margin-right: 5px;
            color: $base-c;
            vertical-align: middle; } } }
