.container {
    position: relative;
    width: 1300px;
    margin-right: auto;
    margin-left: auto; }

.layout-section {
    margin-top: 40px; }

.base-layout {
    display: flex;
    align-items: flex-start;

    &__main {
        width: 860px;
        flex-shrink: 0;
        margin-right: 20px; }

    &__aside {
        width: 420px;
        margin-left: auto;
        padding-left: 40px;
        flex-shrink: 0; } }

.aside-widget {
    &:not(:last-child) {
        margin-bottom: 20px; } }

.light-bg {
    background-color: #F5F6F7;
    padding-bottom: 40px; }

.light-bg + .layout-section,
.light-bg + footer {
    margin-top: 0; }

.base-3items-grid {
    display: grid;
    grid-template-columns: 310px 310px 640px;
    grid-gap: 20px;
    margin-top: 20px; }

.base-3items-grid-inverse {
    display: grid;
    grid-template-columns: 640px 310px 310px;
    grid-gap: 20px;
    margin-top: 20px; }

.base-4items-grid {
    display: grid;
    grid-template-columns: repeat(4, 310px);
    grid-gap: 20px;
    margin-top: 20px; }
